"use client"

import React, { useEffect, useState } from "react"
import { Header } from "@/components/navigation/header"
import { MobileNav } from "@/components/navigation/mobile-nav"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { useToast } from "@/hooks/use-toast"
import { useRouter } from "next/navigation"
import { useAuthStore } from "@/lib/auth-store"
import {
  User,
  Package,
  Calendar,
  Heart,
  Settings,
  LogOut,
  Mail,
  Phone,
  Edit2,
  Check,
  X,
  ShoppingBag,
  Clock,
  Loader2,
} from "lucide-react"
import { useAuthSafe } from "@/hooks/useAuthSafe"

export default function AccountPage() {
  const router = useRouter()
  const { toast } = useToast()
  const { user, isAuthenticated, isLoading } = useAuthSafe()
  const { logout, updateProfile } = useAuthStore()

  const [activeSection, setActiveSection] = useState("profile")
  const [isEditing, setIsEditing] = useState(false)
  const [isSaving, setIsSaving] = useState(false)

  const [formData, setFormData] = useState({
    username: "",
    email: "",
    telephone: "",
  })

  // Redirection après hydratation si non connecté
    // CORRIGÉ : on ne redirige QUE quand on est sûr que l’hydratation est finie
  useEffect(() => {
    // Tant que l’hydratation n’est pas terminée → on ne fait RIEN
    if (isLoading) return

    // Maintenant on est sûr : si l’utilisateur n’est pas connecté → on le redirige
    if (!isAuthenticated) {
      router.push("/auth/login")
    }
  }, [isLoading, isAuthenticated, router])

  // Synchro du formulaire avec les données utilisateur
  useEffect(() => {
    if (user) {
      setFormData({
        username: user.username || "",
        email: user.email || "",
        telephone: user.telephone || "",
      })
    }
  }, [user])

  const handleSave = async () => {
    setIsSaving(true)
    try {
      const success = await updateProfile({
        username: formData.username.trim(),
        email: formData.email.trim(),
        telephone: formData.telephone.trim() || undefined,
      })

      if (success) {
        toast({
          title: "Profil mis à jour !",
          description: "Vos informations ont été enregistrées avec succès.",
        })
        setIsEditing(false)
      } else {
        toast({
          title: "Échec",
          description: "La mise à jour a échoué. Veuillez réessayer.",
          variant: "destructive",
        })
      }
    } catch (error) {
      toast({
        title: "Erreur",
        description: "Une erreur est survenue lors de la sauvegarde.",
        variant: "destructive",
      })
    } finally {
      setIsSaving(false)
    }
  }

  const handleCancel = () => {
    if (user) {
      setFormData({
        username: user.username || "",
        email: user.email || "",
        telephone: user.telephone || "",
      })
    }
    setIsEditing(false)
  }

  const handleLogout = () => {
    logout()
    toast({ title: "Déconnexion réussie", description: "À bientôt sur NappyLocks !" })
    router.push("/")
  }

  // Données mock (à remplacer par l'API plus tard)
  const orders = [
    { _id: "CMD-001", date: "15 Jan 2025", status: "Livrée", total: 47.99, items: 2 },
    { _id: "CMD-002", date: "10 Jan 2025", status: "En cours", total: 35.0, items: 1 },
  ]

  const appointments = [
    { _id: "RDV-001", date: "25 Jan 2025", time: "14:00", service: "Tresses Box Braids", status: "Confirmé" },
    { _id: "RDV-002", date: "10 Fév 2025", time: "10:00", service: "Locks Naturelles", status: "Confirmé" },
  ]

  const navItems = [
    { id: "profile", label: "Profil", icon: User },
    { id: "orders", label: "Commandes", icon: Package },
    { id: "appointments", label: "Rendez-vous", icon: Calendar },
    { id: "favorites", label: "Favoris", icon: Heart },
  ]

  // RENDU CONDITIONNEL APRÈS TOUS LES HOOKS

  // Pendant l'hydratation
  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <Loader2 className="w-12 h-12 animate-spin text-gold" />
      </div>
    )
  }

  // Si non connecté (après hydratation)
  if (!isAuthenticated || !user) {
    return null // Le useEffect redirigera
  }

  // RENDU PRINCIPAL
  return (
    <div className="min-h-screen bg-gradient-to-b from-muted/50 to-background">
      <Header />

      <div className="container mx-auto px-4 py-8 max-w-7xl">
        {/* En-tête avec avatar */}
        <div className="mb-12 text-center md:text-left">
          <div className="flex flex-col md:flex-row items-center gap-6">
            <Avatar className="w-28 h-28 border-4 border-background shadow-2xl">
              <AvatarFallback className="text-3xl font-bold bg-gradient-to-br from-gray-500 to-black-600 text-white">
                {user.username?.[0]?.toUpperCase()}
              </AvatarFallback>
            </Avatar>
            <div>
              <h1 className="text-4xl font-bold bg-gradient-to-r from-black to-black-600 bg-clip-text text-transparent">
                Bonjour, {user.username.split(" ")[0]} !
              </h1>
              <p className="text-muted-foreground text-lg mt-2">Gérez votre compte et vos réservations en toute simplicité</p>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
          {/* Sidebar Desktop */}
          <aside className="hidden lg:block">
            <Card className="sticky top-24 shadow-xl">
              <CardContent className="p-6">
                <nav className="space-y-2">
                  {navItems.map((item) => (
                    <Button
                      key={item.id}
                      variant={activeSection === item.id ? "default" : "ghost"}
                      className="w-full justify-start h-12 text-left font-medium"
                      onClick={() => setActiveSection(item.id)}
                    >
                      <item.icon className="w-5 h-5 mr-3" />
                      {item.label}
                    </Button>
                  ))}
                </nav>

                <Separator className="my-6" />

                <div className="space-y-2">
                  <Button variant="ghost" className="w-full justify-start h-12">
                    <Settings className="w-5 h-5 mr-3" />
                    Paramètres avancés
                  </Button>
                  <Button
                    variant="ghost"
                    className="w-full justify-start h-12 text-destructive hover:text-destructive hover:bg-destructive/10"
                    onClick={handleLogout}
                  >
                    <LogOut className="w-5 h-5 mr-3" />
                    Se déconnecter
                  </Button>
                </div>
              </CardContent>
            </Card>
          </aside>

          {/* Contenu principal */}
          <div className="lg:col-span-3">
            {/* Tabs Mobile */}
            <div className="lg:hidden mb-8">
              <Tabs value={activeSection} onValueChange={setActiveSection}>
                <TabsList className="grid grid-cols-4 w-full rounded-xl bg-muted/70">
                  {navItems.map((item) => (
                    <TabsTrigger key={item.id} value={item.id} className="flex flex-col py-4 text-xs font-medium">
                      <item.icon className="w-5 h-5 mb-1" />
                      {item.label}
                    </TabsTrigger>
                  ))}
                </TabsList>
              </Tabs>
            </div>

            {/* === PROFIL === */}
            {activeSection === "profile" && (
              <Card className="overflow-hidden shadow-2xl border-0">
                <div className="bg-gradient-to-r from-black to-black-600 p-3 text-white">
                  <div className="flex items-center justify-between">
                    <div>
                      <CardTitle className="text-2xl">Informations personnelles</CardTitle>
                      <CardDescription className="text-white/90 mt-1">
                        Mettez à jour vos coordonnées
                      </CardDescription>
                    </div>
                    {!isEditing ? (
                      <Button variant="secondary" onClick={() => setIsEditing(true)}>
                        <Edit2 className="w-4 h-4 mr-2" />
                        Modifier
                      </Button>
                    ) : (
                      <div className="flex gap-2">
                        <Button size="sm" variant="ghost" onClick={handleCancel} disabled={isSaving}>
                          <X className="w-5 h-5" />
                        </Button>
                        <Button size="sm" onClick={handleSave} disabled={isSaving}>
                          {isSaving ? <Loader2 className="w-5 h-5 animate-spin" /> : <Check className="w-5 h-5" />}
                        </Button>
                      </div>
                    )}
                  </div>
                </div>

                <CardContent className="p-8 space-y-8">
                  {isEditing ? (
                    <div className="space-y-6">
                      <div>
                        <Label htmlFor="username" className="text-base font-medium">Nom complet</Label>
                        <Input
                          id="username"
                          value={formData.username}
                          onChange={(e) => setFormData({ ...formData, username: e.target.value })}
                          className="mt-2 h-12"
                          placeholder="Votre nom complet"
                        />
                      </div>
                      <div>
                        <Label htmlFor="email" className="text-base font-medium">Email</Label>
                        <Input
                          id="email"
                          type="email"
                          value={formData.email}
                          onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                          className="mt-2 h-12"
                        />
                      </div>
                      <div>
                        <Label htmlFor="telephone" className="text-base font-medium">Téléphone (optionnel)</Label>
                        <Input
                          id="telephone"
                          type="tel"
                          value={formData.telephone}
                          onChange={(e) => setFormData({ ...formData, telephone: e.target.value })}
                          className="mt-2 h-12"
                          placeholder="+225 6 .. .. .. .."
                        />
                      </div>
                    </div>
                  ) : (
                    <div className="space-y-8">
                      <div className="flex items-center gap-5">
                        <div className="p-3 bg-black dark:bg-amber-900 rounded-full">
                          <User className="w-6 h-6 text-amber-200 dark:text-amber-400" />
                        </div>
                        <div>
                          <p className="text-sm text-muted-foreground">Nom complet</p>
                          <p className="text-xl font-semibold">{user.username}</p>
                        </div>
                      </div>
                      <Separator />
                      <div className="flex items-center gap-5">
                        <div className="p-3 bg-black dark:bg-amber-400 rounded-full">
                          <Mail className="w-6 h-6 text-amber-200 dark:text-amber-400" />
                        </div>
                        <div>
                          <p className="text-sm text-muted-foreground">Email</p>
                          <p className="text-xl font-semibold">{user.email}</p>
                        </div>
                      </div>
                      {user.telephone && (
                        <>
                          <Separator />
                          <div className="flex items-center gap-5">
                            <div className="p-3 bg-emerald-100 dark:bg-emerald-900 rounded-full">
                              <Phone className="w-6 h-6 text-emerald-600 dark:text-emerald-400" />
                            </div>
                            <div>
                              <p className="text-sm text-muted-foreground">Téléphone</p>
                              <p className="text-xl font-semibold">{user.telephone}</p>
                            </div>
                          </div>
                        </>
                      )}
                    </div>
                  )}
                </CardContent>
              </Card>
            )}

            {/* === COMMANDES === */}
            {activeSection === "orders" && (
              <div className="space-y-6">
                <h2 className="text-3xl font-bold mb-6">Mes commandes</h2>
                {orders.map((order) => (
                  <Card key={order._id} className="p-6 hover:shadow-lg transition-shadow">
                    <div className="flex flex-col md:flex-row justify-between gap-6">
                      <div className="flex items-start gap-5">
                        <div className="p-3 bg-black rounded-full">
                          <ShoppingBag className="w-7 h-7 text-amber-400" />
                        </div>
                        <div>
                          <p className="font-bold text-xl">{order._id}</p>
                          <p className="text-muted-foreground">
                            {order.date} • {order.items} article{order.items > 1 ? "s" : ""}
                          </p>
                          <Badge variant={order.status === "Livrée" ? "default" : "secondary"} className="mt-3">
                            {order.status}
                          </Badge>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="text-3xl font-bold">{order.total.toFixed(2)} €</p>
                        <Button variant="outline" className="mt-4">Voir le détail</Button>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            )}

            {/* === RENDEZ-VOUS === */}
            {activeSection === "appointments" && (
              <div className="space-y-6">
                <h2 className="text-3xl font-bold mb-6">Prochains rendez-vous</h2>
                {appointments.map((apt) => (
                  <Card key={apt._id} className="p-6">
                    <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-6">
                      <div className="flex items-start gap-5">
                        <div className="p-3 bg-amber-400 rounded-full">
                          <Clock className="w-7 h-7 text-black" />
                        </div>
                        <div>
                          <h3 className="text-xl font-bold">{apt.service}</h3>
                          <p className="text-muted-foreground mt-1">{apt.date} à {apt.time}</p>
                          <Badge className="mt-3 bg-green-600">Confirmé</Badge>
                        </div>
                      </div>
                      <div className="flex gap-3 w-full md:w-auto">
                        <Button variant="outline" className="flex-1 md:flex-initial">Modifier</Button>
                        <Button variant="destructive" size="icon">
                          <X className="w-4 h-4" />
                        </Button>
                      </div>
                    </div>
                  </Card>
                ))}
              </div>
            )}

            {/* === FAVORIS === */}
            {activeSection === "favorites" && (
              <Card className="text-center py-20">
                <Heart className="w-20 h-20 mx-auto text-muted-foreground mb-6" />
                <h3 className="text-3xl font-bold mb-4">Aucun favori pour le moment</h3>
                <p className="text-muted-foreground text-lg max-w-md mx-auto mb-8">
                  Ajoutez vos produits préférés en cliquant sur le cœur dans la boutique.
                </p>
                <Button size="lg" asChild>
                  <a href="/shop">Explorer la boutique</a>
                </Button>
              </Card>
            )}
          </div>
        </div>
      </div>

      <MobileNav />
    </div>
  )
}