// hooks/useAuthSafe.ts
"use client"

import { useEffect, useState } from "react"
import { useAuthStore } from "@/lib/auth-store"

export const useAuthSafe = () => {
  const [isHydrated, setIsHydrated] = useState(false)

  useEffect(() => {
    // On regarde directement si le store a déjà des données
    const check = () => {
      const state = useAuthStore.getState()
      if (state.user || state.token !== null) {
        setIsHydrated(true)
      }
    }

    // Vérification immédiate
    check()

    // On écoute les changements du store
    const unsub = useAuthStore.subscribe(() => {
      check()
    })

    // Fallback de sécurité (max 1 seconde)
    const timeout = setTimeout(() => {
      setIsHydrated(true)
      unsub()
    }, 1000)

    return () => {
      unsub()
      clearTimeout(timeout)
    }
  }, [])

  const user = useAuthStore((s) => s.user)
  const token = useAuthStore((s) => s.token)
  const isAuthenticated = useAuthStore((s) => s.isAuthenticated)

  return {
    user,
    token,
    isAuthenticated: isHydrated ? isAuthenticated : false,
    isLoading: !isHydrated,
  }
}